<template>
    <div class="chatWindow">
        <div v-for="m in this.messages" v-bind:key="m.id">
            <div v-if="m.self === true">        
                <message-to :content="m.msg" :sender="m.username" :timestamp="moment(m.timestamp)"/>
            </div>
            <div v-else>
                <message-from :content="m.msg" :sender="m.username" :timestamp="moment(m.timestamp)"/>
            </div>
        </div>
    </div>
</template>

<script>
import MessageTo from './Message/MessageTo';
import MessageFrom from './Message/MessageFrom';
import moment from 'moment';
export default {
    name: 'ChatWindow',
    components: {
        MessageTo,
        MessageFrom
    },
    data: function () {
        return {
            messages: []
       }
    },
    mounted(){
        this.$socket.on('load data', data => {
            this.messages = data;
        })
    },
    methods: {
        addMessage(msg) {
            this.messages.push(msg);
        },
        addFile(file) {
            this.messages.push(file);
        },
        moment(date) {
            return moment(date).format('DD.MM.YYYY LT');
        }
    }
}
</script>

<style scoped>
    .chatWindow {
        margin-top: 4em;
    }
</style>