<template>
    <div class="inputBar">
        <b-input-group>
                <b-form-input v-model="message" placeholder="Enter your message"/>
                <b-input-group-append>
                    <b-form-file v-model="file"/>
                    <b-button v-on:click="sendMessage()"><send-icon/></b-button>
                </b-input-group-append>
        </b-input-group>
    </div>
</template>

<script>
import SendIcon from 'vue-material-design-icons/Send.vue';
export default {
    name: 'InputBar',
    components: {
        SendIcon
    },
    data: function() {
        return {
            message: '',
            file: null
        }
    },
    methods: {
        sendMessage() {
            if(this.message != null){
                this.$emit('message', this.message);
            }
        },
        sendFile() {
            if(this.file != null){
                this.$emit('file', this.file);
            }
        }
    }
}
</script>

<style scoped>
    .inputBar {
        margin: 2em;
        width: 97%;
        position:fixed; /* fixing the position takes it out of html flow - knows
                   nothing about where to locate itself except by browser
                   coordinates */
        bottom: 0;           /* bottom left corner should start at topmost spot */
        left: 0;
        z-index:200;  /* high z index so other content scrolls underneath */   
    }
</style>