<template>
    <div class="ChatRoom">
        <NavBar/>
        <chat-window ref="chatWindow"/>
        <input-bar @message="onMessage" @file="onFile" />
    </div>
</template>

<script>
import InputBar from './ChatRoom/InputBar.vue';
import NavBar from './ChatRoom/NavBar.vue';
import ChatWindow from './ChatRoom/ChatWindow.vue'
export default {
    name: 'ChatRoom',
    components: {
        InputBar,
        NavBar,
        ChatWindow
    },
    props: {
        roomId: String
    },
    data: function() {
        return {
            messages: []
        }
    },
    created() {
        if (!localStorage.username) {
            this.$router.push({name: 'main', params: { error: 'no username was set' }});
            return;
        }
        if (!this.roomId) {
            this.$router.push({name: 'main', params: { error: 'no room id was set' }});
            return;
        }
        this.$socket.emit('join room', this.roomId, localStorage.username);
        this.$socket.on('joined room', (joinedRoomId, username) => {
            this.$socket.roomId = joinedRoomId;
            this.$socket.username = username;
        });
        this.$socket.on('err', err => {
            this.$router.push({name: 'main', params: { error: err }});
            return;
        });  
    },
    mounted(){
        this.listen();
    },
    methods: {
        onMessage (message) {
            this.$socket.emit('message', message)
            this.$socket.on('message success', msg => {
                msg.self = true;
                this.$refs.chatWindow.addMessage(msg);
            });
        },
        onFile(file) {
            this.$socket.emit('file', file)
            this.$socket.on('file success', file => {
                file.self = true;
                this.$refs.chatWindow.addFile(file);
            });
        },
        listen() {
            this.$socket.on('message', msg => {
                this.$refs.chatWindow.addMessage(msg);
            })
        }
    },
    beforeRouteLeave(to, from, next) {
        this.$socket.emit('leave room');
        next();
    },
    beforeDestroy() {
        this.$socket.emit('leave room');
    }
}
</script>

