<template>
  <div class="main-view">
    <div class="error-alert">
        <b-alert v-if="this.error || $route.params.error" variant="danger" show>{{ this.error || $route.params.error }}</b-alert>
    </div>
    <div class="title-div">
        <h1 class="title-msg">Quickchat</h1>
    </div>
    <form class="space-inbetween-above">
        <div class="first-line-div">
            <input type="text" v-model="username" placeholder="Choose username"/>
        </div>
        <div class="second-line-div space-inbetween-above">
            <input class="input-chat-id-class" type="text" v-model="roomId" placeholder="Room ID"/>
            <b-button id="join-id" class="orange-button-class space-inbetween-left" type="button" variant="danger" v-on:click="joinRoom()">JOIN</b-button>
            <b-button id="join-all-id" class="orange-button-class space-inbetween-left" type="button" variant="danger" v-on:click="joinRoom('all')">JOIN ALL</b-button>
        </div>
    </form>
  </div>
</template>

<script>
export default {
  name: 'MainView',
  data: function() {
    return {
      error: '',
      username: '',
      roomId: ''
    }
  },
  created() {
    if (localStorage.username) {
      this.username = localStorage.username;
    }
  },
  methods: {
    joinRoom(roomId) {
      if (!roomId && !this.roomId) {
        this.error = 'room id cannot be empty';
      } else {
        this.error = '';
        roomId = roomId || this.roomId;
        localStorage.username = this.username;
        this.$router.push(`/room/${roomId}`);
      }
    }
  },
  beforeRouteLeave(to, from, next) {
        this.$error = '';
        next();
    }
}
</script>

